

/*------------------------@use------------------------------------------*/
@use "nav";
@use "animation";
@use "variabels";
@use "mixin";


/*------------------------Dark------------------------------------------*/

@media (prefers-color-scheme:dark) {

  body {
    background-color: variabels.$darktheme-background !important;
    font-family: variabels.$font-style;
    width: 100vw;
  }
  header {
    background-color: variabels.$headerbgdark-color !important;
    width: 100vw;
    margin-left: -1vw;
    margin-top: -2vh;
  }

/*----------------------Bilder grids--------------------------------------------*/
.picGrid {
  @include mixin.grid1x2;
  

  img {
    @include mixin.imageDark;
}
}

.picGrid2 {
  @include mixin.grid1x2;
  

  img {
    @include mixin.imageDark (50vw);
    
}
}

/*------------------------h1------------------------------------------*/
.h1Change{
  @include mixin.h1Change;
}

/*------------------------SASS------------------------------------------*/
.SASS {
  width: 50vw;
  margin: auto;

  h1{
    @include mixin.h1Change;
  }

  p{
    color: variabels.$text-color !important;
    text-align: left;
  }
}

/*------------------------Krav------------------------------------------*/
.hemsidaKrav {
  @include mixin.hemsidaText;

  h2{
    color: variabels.$text-color !important;
    text-decoration: underline;
  }

  p{
    color: variabels.$text-color !important;
  }
}


.sassKrav {
  @include mixin.hemsidaText;

}
}


 /*------------------------Light------------------------------------------*/


@media (prefers-color-scheme:light) {

  body {
    background-color: variabels.$lighttheme-background;
    font-family: variabels.$font-style;
    width: 100vw;
  }
  header {
    background-color: variabels.$headerbg-color;
    width: 100vw;
    margin-left: -1vw;
    margin-top: -2vh;
  }

/*----------------------Bilder grids--------------------------------------------*/
.picGrid {
  @include mixin.grid1x2;
  

  img {
    @include mixin.imageLight;
}
}

.picGrid2 {
  @include mixin.grid1x2;
  

  img {
    @include mixin.imageLight (50vw);
    
}
}

/*------------------------h1------------------------------------------*/
#h1Change{
  @include mixin.h1Change;
}

/*------------------------SASS------------------------------------------*/
