/*------------------------Animationer Text------------------------------------------*/
  
  #animationText {
    text-align: center;
    width: fit-content;
    font-size: 400%;
    animation-name: changeTextColor;
    animation-duration: 2s;
    animation-iteration-count: infinite;
    animation-direction: alternate;
    border-radius: 50%;
    padding: 2%;
    position: relative;
  
  }
  
  
  /*------------------------Animationer Knapp------------------------------------------*/

